<template>
  <div class="review">
    <v-container>
      <v-alert border="left" colored-border color="pink accent-2" elevation="2">
        Please first select an aspect first, then type in the text area. We will
        automatically compose the review for you based on what you have typed.
        When you are typing, you can refer to the right panel to select some
        prompted content we generate for you. Please refer to
        <a
          href="https://github.com/neulab/ReviewAdvisor/blob/main/materials/AnnotationGuideline.pdf"
          >this</a
        >
        if you are not sure about the aspects we have defined here.
      </v-alert>
      <v-row align="center">
        <v-col class="d-flex" cols="3" sm="3">
          <v-select
            :items="aspects"
            label="Select Aspects"
            outlined
            v-model="selectedAspect"
            @change="changeSelect"
          ></v-select>
        </v-col>
      </v-row>
    </v-container>
    <v-container>
      <v-row>
        <v-col class="d-flex" cols="3" sm="7">
          <v-textarea
            :filled="selectedAspect == 'No Aspect Selected'"
            :disabled="selectedAspect == 'No Aspect Selected'"
            outlined
            name="input-7-4"
            label="Review Aspect Text"
            v-model="content"
            placeholder="Type to start writing review.."
            rows="7"
          ></v-textarea
        ></v-col>
        <v-col>
          <v-card class="mx-auto" max-width="400" tile>
            <v-list dense>
              <v-subheader>HINTS</v-subheader>
              <v-list-item-group color="primary">
                <v-list-item
                  v-for="(hint, i) in hints"
                  :key="i"
                  @click="autoComplete(i)"
                >
                  <v-list-item-content>
                    <v-list-item-title
                      class="text-wrap"
                      v-text="hint.text"
                    ></v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list> </v-card
        ></v-col>
      </v-row>

      <div>
        <h3>Review</h3>
        <div v-html="review">{{ review }}</div>
      </div>
    </v-container>
  </div>
</template>

<script>
export default {
  components: {},
  methods: {
    changeSelect() {
      if (this.selectedAspect in this.aspect_content_dict) {
        this.content = this.aspect_content_dict[this.selectedAspect];
      } else {
        this.content = "";
      }
    },
    autoComplete(i) {
      this.content += this.hints[i].text;
    },
  },
  data() {
    return {
      aspect_content_dict: {}, // Should be a key-value pair dict
      selectedAspect: "No Aspect Selected",
      content: "",
      review: "",
      aspects: [
        "Summary",
        "Motivation",
        "Originality",
        "Soundness",
        "Substance",
        "Replicability",
        "Meaningful Comparison",
        "Clarity",
        "Others",
      ],
      selectedHintIdx: null,
      hints: [
        { text: "", id: 0 },
        { text: "", id: 1 },
        { text: "", id: 2 },
      ],
    };
  },
  watch: {
    content: function (newContent) {
      // Change the aspect_content_dict
      this.aspect_content_dict[this.selectedAspect] = newContent;
      this.review = "";
      for (var key in this.aspect_content_dict) {
        const current_aspect_review = this.aspect_content_dict[key];
        if (current_aspect_review != "") {
          this.review +=
            key.toUpperCase() +
            "</br>" +
            this.aspect_content_dict[key] +
            "</br></br>";
        }
      }
      // Change the hints
      var words = [
        "The sky",
        "above",
        "the port",
        "was",
        "the color of television",
        "tuned",
        "to",
        "a dead channel",
        ".",
        "All",
        "this happened",
        "more or less",
        ".",
        "I",
        "had",
        "the story",
        "bit by bit",
        "from various people",
        "and",
        "as generally",
        "happens",
        "in such cases",
        "each time",
        "it",
        "was",
        "a different story",
        ".",
        "It",
        "was",
        "a pleasure",
        "to",
        "burn",
      ];

      function randomSentence() {
        var n = 10;
        var sentence = "";
        while (n--) {
          sentence += words[Math.floor(Math.random() * words.length)] + " ";
        }
        return sentence;
      }
      this.hints[0].text = randomSentence();
      this.hints[1].text = randomSentence();
      this.hints[2].text = randomSentence();
    },
  },
};
</script>
